@charset "UTF-8";
.sidebar-button-wrapper {
  background-size: 24px auto;
  background-repeat: no-repeat;
  padding-left: 32px;
  height: 24px;
  line-height: 23px; }

/* Common */
* {
  font-family: Dotum, "돋움", Helvetica, "Apple SD Gothic Neo", sans-serif;
  margin: 0;
  padding: 0; }

html {
  width: 100%;
  height: 100%;
  -webkit-text-size-adjust: none; }

div {
  cursor: default; }

li {
  list-style: none; }

img,
fieldset {
  border: none;
  vertical-align: middle; }

input {
  font-size: 14px;
  color: #000;
  vertical-align: middle;
  opacity: 1; }

select,
option,
textarea {
  font-size: 14px;
  color: black;
  vertical-align: middle; }

a,
a:link,
a:visited {
  text-decoration: none;
  color: black; }

a:hover {
  color: #fe4e6c; }

a:active {
  text-decoration: none; }

table {
  border-spacing: 0;
  border: 0;
  border-collapse: collapse;
  width: 100%;
  height: 100%; }

button {
  padding: 0;
  margin: 0;
  border: 0;
  cursor: pointer;
  overflow: visible; }

strong {
  font-weight: bold; }

body {
  padding: 0;
  margin: 0;
  text-decoration: none;
  color: #000;
  font-weight: normal;
  line-height: 160%;
  font-size: 14px; }

img {
  border: none;
  vertical-align: middle;
  max-width: 100%; }

input {
  height: 32px;
  border: 1px solid #bfbfbf;
  padding: 5px 10px;
  box-sizing: border-box; }
  input[type="checkbox"] {
    padding: 0;
    height: auto; }
  input[type="radio"] {
    padding: 0;
    height: auto;
    border: none; }
  input[type="text"], input[type="email"], input[type="password"], input[type="tel"], input[type="number"] {
    border-radius: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; }
  input:disabled {
    background-color: #f2f2f2; }

input#password {
  padding: 0;
  border: 0;
  border-bottom: 2px #e1e1e1 solid; }

input[type="radio"],
input[type="button"] {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

input[type="button"] {
  border-radius: 0; }

caption {
  visibility: visible;
  position: absolute;
  left: -1000px;
  top: -1000px;
  height: 0;
  width: 0;
  display: none; }

body {
  color: #000; }

ul {
  padding: 0; }

li {
  list-style: none; }

b {
  font-weight: bold; }

legend,
caption,
hr {
  height: 0px;
  overflow: hidden;
  font-size: 0em;
  line-height: 0em;
  text-indent: -9999px;
  width: 0;
  margin: -1px 0 0 0;
  position: relative;
  top: -1px; }

colgroup {
  display: table-column-group; }

select {
  width: 200px;
  height: 32px;
  padding-left: 7px;
  padding-right: 20px;
  font-family: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px solid #d8d8d8;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
  background: url("https://danoshop.net/images/common/selection-arrow.svg") no-repeat right 10px center #fff; }

select::-ms-expand {
  /* for IE 11 */
  display: none; }

input[type="radio"] {
  border: 1px solid #c5c5c5;
  width: 20px;
  height: 20px;
  appearance: none;
  -webkit-appearance: none;
  border-radius: 22px;
  background-color: #fff; }
  input[type="radio"]:after {
    content: "";
    position: relative;
    display: block; }
  input[type="radio"].on {
    border-color: #f86767; }
    input[type="radio"].on:after {
      left: 4px;
      top: 4px;
      width: 10px;
      height: 10px;
      border-radius: 10px;
      background-color: #f86767; }

.displaynone {
  display: none !important; }

.confirm-btn {
  width: calc(100% - 60px);
  height: 45px;
  border-radius: 2px;
  background-color: #f86767;
  color: #fff;
  margin: -20px 0 10px;
  position: absolute; }

.cancel-btn {
  text-align: center;
  width: 100%;
  background-color: transparent;
  text-decoration: underline;
  color: #b0b0b0;
  font-size: 11px;
  margin-top: 45px; }

